interface IconProps {
    width?: string;
    height?: string;
    className?: string;
}

const CloseIcon = ({ width = '12', height = '12', className = '' }: IconProps) => {
    return (
        <span className={className}>
            <svg width={width} height={height} viewBox="0 0 12 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path
                        d="M1 10.9999L10.9999 1"
                       stroke="currentColor"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                    />
                    <path
                        d="M10.9999 10.9999L1 1"
                       stroke="currentColor"
                        strokeWidth="2"
                        strokeLinecap="round"
                        strokeLinejoin="round"
                    />
                </svg>
        </span>
    );
};

export default CloseIcon;
